.container {
  display: flex;
}

.wrapper {
  display: flex;
}

.container_small {
  padding: 0.2rem 0;
}

.container_medium {
  padding: 0.5rem 0;
}

.container_large {
  padding: 0.8rem 0;
}

.votes {
  font-weight: 400;
  margin-left: 0.2rem;
  letter-spacing: 0;
  line-height: 1.1;
}

.votes_small {
  font-size: 1rem;
}

.votes_medium {
  font-size: 1.25rem;
}

.votes_large {
  font-size: 1.5rem;
}